                  li
                    i.fas.fa-circle-check
                    | Mirar el tiempo de permanencia como clientes.

                  li
                    i.fas.fa-circle-check
                    | Su distribución geográfica.

                  li
                    i.fas.fa-circle-check
                    | La posibilidad de generar boca a boca.

                  li
                    i.fas.fa-circle-check
                    | Tener en cuenta el comportamiento de compra.
          
          
